import React, { useState, useEffect, useMemo } from "react";
import { useStyles, Container, Header, Body } from "./styles";
import Layout from "../../components/Layout";
import TableLive from "../../components/TableLive";
import RadioButtonChecked from "@material-ui/icons/RadioButtonChecked";
import "../../assets/extra.css?v=1.9.0";
import "../../assets/lol.css?v=1.9.0";

import { Month, Week } from "./utils";
import api from "../../api";
import axios from "axios";

const Managers = () => {
  const classes = useStyles();
  const [matches, setMatches] = useState([]);
  var gamesList = [];
  useEffect(() => {
    async function fetchData() {
      const res2 = await axios.get("https://oe.datalisk.io/matches/live/", {
        headers: {
          "x-api-key": "f561197a-82ea-4e54-acd2-386979018a7a",
        },
      });
      return res2.data;
    }
    const getData = async (data) => {
      const res = await Promise.all(
        data.map(async (event) => {
          const res2 = await api.get(
            `/getEventDetails?hl=pt-BR&id=${event.matchId}`,
            {
              headers: {
                "x-api-key": "0TvQnueqKa5mxJntVWt0w4LpLfEkrV1Ta8rQBb9Z",
              },
            }
          );
          const games = res2.data.data.event.match.games;

          let item = event;
          console.log('event', event)
          games.map((game) => {
            if (game.state === "inProgress") {
             
              let team1NumberId = Number(
                event.team1Logo?.split(".")[0]?.split("Alternative")[0]
              );
              let team2NumberId = Number(
                event.team2Logo?.split(".")[0]?.split("Alternative")[0]
              );

              item = {
                ...item,
                count: game.number,
                gameId: game.id,
                team1NumberId: team1NumberId,
                team2NumberId: team2NumberId,
                time: new Date(event.startTime).getHours(),
              };
            }
          });
          return item;
        })
      );

      return res;
    };
    fetchData().then((res) => {
      getData(res).then((res) => {
        setMatches(res);
      });
    });
  }, []);

  return (
    <div>
      <div className={classes.root}>
        <Layout />
        <main className={classes.content}>
          <div className={classes.toolbar} />
          <Container>
            <div style={{ marginBottom: "75px" }}>
              <Header>
                <RadioButtonChecked
                  style={{
                    color: "#fff",
                    height: "1.92857143rem",
                    fontSize: "2.3125rem",
                    lineHeight: "1.92857143rem",
                    marginRight: "5px",
                  }}
                />
                <h1 className={classes.cardTitleWhite}>Ao Vivo</h1>
              </Header>
              <Body>
                <TableLive tableHeaderColor="primary" tableData={matches} />
              </Body>
            </div>
          </Container>
        </main>
      </div>
    </div>
  );
};

export default Managers;
